import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router';
import {MusicianService} from '../../services/musician/musician.service';

@Component({
  selector: 'app-category',
  templateUrl: './category.page.html',
  styleUrls: ['./category.page.scss'],
})
export class CategoryPage implements OnInit {
    
    
    public category:any;
    public musicians:Array<any>;
    
    constructor(private route:ActivatedRoute, private musicianService:MusicianService) { 
        let categoryId = this.route.snapshot.params['id'];
        this.category = {id:categoryId, name:"", loading:true};
        
        
        this.musicianService.getCategory(this.category.id).then((data) => {
            console.log(data);
            this.category = data;
            this.musicians = data["profiles"];
        })
        
    }

    ngOnInit() {
    }


    public calculateFeedback(musician){
        let rating = 100;
        
        if (musician.reviews){
            let reviewTotal = 0;
            for (let review of musician.reviews){
                
                reviewTotal = reviewTotal + (review.rating*20);
                
                
            }
            
            
            reviewTotal = (reviewTotal / musician.reviews.length);
            
            rating = reviewTotal;
        
        }
        
        return rating.toFixed(0);
        
    }


}
